.center{
	margin-top: 50px;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 29vw;
	background: white;
	border-radius: 10px;
}
.center h1{
	text-align: center;
	padding: 0 0 20px 0;
	border-bottom: 1px solid silver;
}
.center .form{
	padding: 0 40px;
	box-sizing: border-box;
}
.form .txt_field{
	position: relative;
	border-bottom: 2px solid #adadad;
	margin: 30px 0;
}
.txt_field input{
	width: 100%;
	padding: 0 5px;
	height: 40px;
	font-size: 1rem;
	border: none;
	background: none;
	outline: none;
}
.txt_field label{
	position: absolute;
	top: 50%;
	left: 5px;
	color: #adadad;
	transform: translateY(-50%);
	font-size: 1rem;
	pointer-events: none;
}
.txt_field span::before{
	content: '';
	position: absolute;
	top: 40px;
	left: 0;
	width: 0px;
	height: 2px;
	background: #ffe5cc;
	transition: .5s;
}
.txt_field input:focus ~ label,
.txt_field input:valid ~ label{
	top: -5px;
	color: #ffe5cc;
}
.txt_field input:focus ~ span::before,
.txt_field input:Valid ~ span::before{
	width: 100%;
}
.pass{
	margin: -5px 0 20px 5px;
	color: #a6a6a6;
	cursor: pointer;
}
.pass:hover{
	text-decoration: underline;
}
.submit{
	width: 100%;
	height: 50px;
	border: 1px solid;
	border-radius: 25px;
	font-size: 1rem;
	font-weight: 700;
	cursor: pointer;
}
.submit:hover{
	background: #ffe5cc;
	color: #e9f4fb;
	transition: .5s;
}
.signup_link{
	margin: 30px 0;
	text-align: center;
	font-size: 1rem;
	color: #666666;
}
.signup_link a{
	color: #2691d9;
	text-decoration: none;
}
.signup_link a:hover{
	text-decoration: underline;
}

.selectSex {
	width: 100%;
	margin-top: 40px;
	background: none;
	border: none;
}

.pError {
	color: #ff0000;
	font-size: 1rem;
}
.restoreSubmit {
	margin-bottom: 30px;
}

.checkboxDelete {
	margin-bottom: 30px;
	font-size: 1rem;
}

@media only screen /* 320px — 480px */
and (min-device-width : 320px) 
and (max-device-width : 480px) {
	.center{
		width: 80vw;
		margin-top: 0;
	}
	.center h1{
		font-size: 1.5rem;
	}
	.txt_field input{
		font-size: 0.8rem;
	}
	.txt_field label{
		font-size: 0.8rem;
	}
	.signup_link{
		font-size: 0.8rem;
	}
	.submit {
		font-size: 0.9rem;
	}
}

@media only screen /* 481px — 768px */
and (min-device-width : 481px) 
and (max-device-width : 768px) {
	.center{
		width: 70vw;
	}
	.center h1{
		font-size: 1.5rem;
	}
	.txt_field label{
		font-size: 1rem;
	}
	.signup_link{
		font-size: 0.8rem;
	}
}

@media only screen /* 769px — 1024px */
and (min-device-width : 760px) 
and (max-device-width : 1024px) {
	.center{
		width: 43vw;
	}
	.center h1{
		font-size: 1.5rem;
	}
	.txt_field label{
		font-size: 1rem;
	}
	.signup_link{
		font-size: 0.8rem;
	}
}

@media only screen /* 1025px — 1200px */
and (min-device-width : 1025px) 
and (max-device-width : 1200px) {
	.center{
		width: 35vw;
	}
	.signup_link{
		font-size: 0.8rem;
	}
}

@media only screen /* 1201px - 1823px */
and (min-device-width : 1201px) 
and (max-device-width : 1823px) {
	.center{
		margin-top: 60px;
	}
}

